.App {
  text-align: center;
  margin: 0 auto;
  width: 280px;
  height: 450px;
  background-color: #ffffff;
  border-radius: 10px;
  margin-top: 50px;
  padding: 10px;
}
h1{
  letter-spacing: 0.1em;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-input{
  width: 200px;
  height: 20px;
  border-radius: 10px;
  padding-left: 7px;
}
.itemList{
  margin: 0 auto;
  width: 200px;
}
.todoItem{
  list-style: none;
  display: flex;
  align-items: center;
  margin: 8px 0;
  position: relative;
}
.todoItem p{
  text-align: left;
  letter-spacing: 0.05em;
}
.todoItem input{
  margin-right: 20px;
}
.checked{
  text-decoration: line-through;
  color: #c3c6ca;
}
.todoItem__dot{
  position: absolute;
  right: 0;
  bottom: 38%;
  width: 3px;
  height: 3px;
  background-color: #929598;
  border-radius: 50%;
}
.todoItem__dot:hover{
  cursor: pointer;
}
.todoItem__dot::before{
  content: '';
  position: absolute;
  display: block;
  right: 0;
  top: 5px;
  width: 3px;
  height: 3px;
  background-color: #929598;
  border-radius: 50%;
} 
.todoItem__dot::after{
  content: '';
  position: absolute;
  display: block;
  right: 0;
  bottom: 5px;
  width: 3px;
  height: 3px;
  background-color: #929598;
  border-radius: 50%;
}